.default-background {
  background-color: #e7e5e4;
}
.frontpage {
  width: 100%;
  height: 100%;
  position: absolute;
}
.frontheader {
  color: white;
  font-size: 92px;
  font-family: times, Times New Roman, times-roman, georgia, serif;
}
.fronttext {
  color: white;
  font-size: 36px;
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
}
.frontbanner {
  top: 40%;
  left: 40%;
  position: absolute;
  animation: slideUp 4s;
}
@keyframes slideUp {
  from {
    top: 45%;
  }
  to {
    top: 40%;
  }
}
